import React from 'react'
import { Link } from 'react-router-dom'
import './NavBar.css'

const NavBar = () => {
  return (
    <nav className="Navbar">
      <ul className="Navbar-links">
        <li>
          <Link to="/authors"></Link>
        </li>
        <li>
          <Link to="/about"> </Link>
        </li>
        <li>
          <Link to="/reviews"></Link>
        </li>
        <li>
          <Link to="/image-gallery"></Link>
        </li>
        <li>
          <Link to="/videos"></Link>
        </li>
      </ul>
    </nav>
  )
}

export default NavBar
